                >
                  <v-col

                      cols="3"
                      v-for="(selection, index) in weekThree" :key="index"
                      class="shrink"
                  >
                    <v-chip
                        label
                        x-small
                    >
                      {{ selection.text }}
                    </v-chip>
                  </v-col>

                </v-row>
              </v-card-text>
            </v-card>
            <v-card
                class="mx-auto"
