            >
              <v-card-text @click="openModal(4)"
                           style="padding-left: 4px;
                                    padding-right: 4px;
                                    padding-bottom: 4px;
                                    padding-top: 4px; margin-bottom: 7px; min-height: 20px">

                <v-row
                    no-gutters
                >
                  <v-col

                      cols="3"
                      v-for="(selection, index) in WeekFour" :key="index"
                      class="shrink"
                  >
                    <v-chip
                        label
