                              :key="selection.text"
                              class="shrink"
                          >
                            <v-chip
                                label
                                x-small
                                @click="deleteChip(i)"
                            >
                              {{ selection.text }}
                            </v-chip>
                          </v-col>
                        </v-row>
                      </v-container>
                    </v-card>
                  </v-tab-item>

                  <v-tab-item :key="3" value="Preservacion">
                    <v-card
