                            justify="start"
                        >
                          <v-col
                              style="padding-right: 0px; padding-left: 2px; background-color: #EBECF0"
                              v-for="item in categories" :key="item.text"
                              @click="Select(item, 3)">

                            <v-chip
                                label
                                x-small
                            >
                              {{ item.text }}
                            </v-chip>
                          </v-col>
                        </v-row>
                      </v-container>

                      <v-list-item-title style="font-size: 14px; color: #1CB2DE" >Seleccionados</v-list-item-title>

                      <v-container class="py-0">
                        <v-row
                            align="center"
                            justify="start"
                        >
                          <v-col
                              v-for="(selection, i) in selections"
                              :key="selection.text"
                              class="shrink"
                          >
