          </v-card-text>



          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
                color="#white"

                @click="save"
            >
              Guardar
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
  </div>

</template>

<script>
export default {
  name: "April",
  props: ['months'],
  data() {
    return {
      week: '',
      weekOne: [],
      weekTwo: [],
      weekThree: [],
      WeekFour: [],
      items: [
        {
          text: '100 H',
        },
        {
          text: 'ISO',
        },
        {
          text: '50 H',
        },
        {
          text: 'HSC',
        },
        {
          text: '3MO',
        },
        {
          text: '6MO',
        },
        {
          text: 'A',
        },
        {
          text: '3A',
        },
        {
          text: 'PRE',
        },
        {
          text: 'INSP',
        },
      ],
      dialog: false,
      search: '',
      selected: [],
      tab: "subscribe",
    }
  },
  computed: {
    categories () {
      const search = this.search.toLowerCase()

      if (!search) return this.items

      return this.items.filter(item => {
        const text = item.text.toLowerCase()

        return text.indexOf(search) > -1
      })
    },
    selections () {
      const selections = []

      for (const selection of this.selected) {
        selections.push(selection)
      }

      return selections
    },
  },
  methods: {
    save() {
      this.dialog = false
      if(this.week  ) {
        this.Select({text: 'DISPONIBLE'}, 1 )
        console.log('dddd')
      }
    },
    openModal(item) {
      this.week = item;
      console.log('ii', item)
      this.dialog = true
    },
    deleteChip(i) {
      this.selected.splice(i, 1)
    },
    Select(item, num) {
      console.log('num', num, this.week, item)
      if(this.week === 1) {
        if(num === 2) {
          this.selected.push(item)
          this.selected.unshift({text: 'M2'})
        } else if (num ===3 ) {
          this.selected.push(item)
          this.selected.unshift({text: 'PRE'})
        } else {
          this.selected.push(item)

        }

      } else if (this.week===2) {
        if(num === 2) {
          this.weekTwo.push(item)
          this.weekTwo.unshift({text: 'M2'})
        } else if (num ===3 ) {
          this.weekTwo.push(item)
          this.weekTwo.unshift({text: 'PRE'})
        } else {
          this.weekTwo.push(item)

        }

      }
      else if (this.week===3) {
        if(num === 2) {
          this.weekThree.push(item)
          this.weekThree.unshift({text: 'M2'})
        } else if (num ===3 ) {
          this.weekThree.push(item)
          this.weekThree.unshift({text: 'PRE'})
        } else {
          this.weekThree.push(item)

        }
      }
      else {
        if(num === 2) {
          this.WeekFour.push(item)
          this.WeekFour.unshift({text: 'M2'})
        } else if (num ===3 ) {
          this.WeekFour.push(item)
          this.WeekFour.unshift({text: 'PRE'})
        } else {
          this.WeekFour.push(item)

        }
      }

    }
  }
}
</script>

