        v-model="dialog"
        width="500"

    >

      <v-card style="background: #EBECF0">
        <v-card-title>
          <h2>Semana 1</h2>
        </v-card-title>

        <v-card-text>
          <v-row>
            <v-col
                cols="8"
                sm="4"
                md="4"
            >

              <v-list-item-title style="font-size: 10px">Fecha de inicio</v-list-item-title>
              <v-list-item-subtitle style="font-weight: bold"> Febrero 20, 2021</v-list-item-subtitle>
            </v-col>
            <v-col
                cols="8"
                sm="4"
                md="4"
            >
              <v-list-item-title style="font-size: 10px">Fecha Fin</v-list-item-title>
              <v-list-item-subtitle style="font-weight: bold"> --/--/--</v-list-item-subtitle>
            </v-col>
          </v-row>
          <br>
          <v-row>
            <v-col
                cols="12"
                sm="12"
                md="12"
            >
              <v-list-item-title style="font-size: 14px; color: #1CB2DE" >Seleccione una condición</v-list-item-title>
              <v-tabs
                  fixed-tabs
                  background-color="#EBECF0"
                  v-model="tab"
              >
                <v-tab href="#Disponible" >
                    Disponible
                </v-tab>

                <v-tab href="#Mantenimiento">
                    Mantenimiento
                </v-tab>
                <v-tab href="#Preservacion">
                  Preservación
                </v-tab>
              </v-tabs>

              <v-tabs-items v-model="tab"  >
                <v-tab-item :key="1" value="Disponible">
                  <v-card flat  style="background-color: #EBECF0">
                    <v-card-text>
                      <v-col
                          cols="5"
                          sm="5"
                          md="5"
                      >
                        <v-text-field
                            label="Hora de vuelo"
                            id="input-2"
                            required
                            placeholder="Horas de vuelo"
                        ></v-text-field>

                      </v-col>
                    </v-card-text>
                  </v-card>
                </v-tab-item>
                <v-tab-item :key="2" value="Mantenimiento" >
                  <v-card
                      flat
                      class="mx-auto"
                      max-width="500"
                      style="background-color: #EBECF0"
                  >
                        <v-container class="py-0">
                          <v-row
                              align="center"
                              justify="start"
                          >
                            <v-col
                                style="padding-right: 0px; padding-left: 2px; background-color: #EBECF0"
                                v-for="item in categories" :key="item.text"
                                @click="selected.push(item)">

                                <v-chip
                                    label
                                    x-small
                                >
                                  {{ item.text }}
                                </v-chip>
                            </v-col>
                          </v-row>
                        </v-container>

