                    <v-container class="py-0">
                      <v-row
                          align="center"
                          justify="start"
                      >
                        <v-col
                            v-for="(selection, i) in selections"
                            :key="selection.text"
                            class="shrink"
                        >
                          <v-chip
                              label
                              x-small
                              @click="deleteChip(i)"
                          >
                            {{ selection.text }}
                          </v-chip>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card>
                </v-tab-item>

                <v-tab-item :key="3" value="Preservacion">
                  <v-card
                      flat
                      class="mx-auto"
                      max-width="500"
                      style="background-color: #EBECF0"
                  >
                    <v-container class="py-0">
                      <v-row
                          align="center"
                          justify="start"
                      >
                        <v-col
                            style="padding-right: 0px; padding-left: 2px; background-color: #EBECF0"
                            v-for="item in categories" :key="item.text"
                            @click="selected.push(item)">

                          <v-chip
                              label
                              x-small
                          >
                            {{ item.text }}
                          </v-chip>
                        </v-col>
                      </v-row>
                    </v-container>

                    <v-list-item-title style="font-size: 14px; color: #1CB2DE" >Seleccionados</v-list-item-title>

                    <v-container class="py-0">
                      <v-row
                          align="center"
                          justify="start"
                      >
                        <v-col
                            v-for="(selection, i) in selections"
                            :key="selection.text"
                            class="shrink"
                        >
                          <v-chip
                              label
                              x-small
                              @click="deleteChip(i)"
                          >
                            {{ selection.text }}
                          </v-chip>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card>
                </v-tab-item>
              </v-tabs-items>
            </v-col>
          </v-row>
        </v-card-text>



        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
              color="#white"

              @click="dialog = false"
          >
            Guardar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>

</div>
</template>

<script>


import yearCard from "@/components/DashboarPlanification/DashboarTask/OrderJobs/yearCard";
export default {
  name: "Tablas",
  data() {
    return {
      dialog: false,
      tab: "subscribe",
      items: [
        {
          text: '100 H',
        },
        {
          text: 'ISO',
        },
        {
          text: '50 H',
        },
        {
          text: 'HSC',
        },
        {
          text: '3MO',
        },
        {
          text: '6MO',
        },
        {
          text: 'A',
        },
        {
          text: '3A',
        },
        {
          text: 'PRE',
        },
        {
          text: 'INSP',
        },
      ],
      loading: false,
      search: '',
      selected: [],
      weekOne: [],
      weekTwo: [],
      weekThree: [],
      WeekFour: [],
      selectionsDisponible: [],
      orderYear: [
        {month: 'Enero'},
        {month: 'Febrero'},
        {month: 'Marzo'},
        {month: 'Abril'},
        {month: 'Mayo'},
        {month: 'Junio'},
        {month: 'Julio'},
        {month: 'Agosto'},
        {month: 'Septiembre'},
        {month: 'Octubre'},
        {month: 'Noviembre'},
        {month: 'Diciembre'},

      ],
      week: [
          {id: 1 , name: 'Uno'},
        {id: 2 , name: 'Dos'},
        {id: 3 , name: 'Tres'},
        {id: 4 , name: 'Cuatro'},
      ],
    }
  },
  components: {

    yearCard

  },
  methods: {
    openModal(week) {
      this.dialog = true
      console.log('week   this.dialog = true', week)
      console.log('index', week)
      if (week === 'Enero') {
        console.log('semana uno')
      } else if (week.id === 2 ) {
        console.log('semana dos ')
      }
      else if (week.id === 3) {
        console.log('semana tres ')
      } else {
        console.log(' semana cuatro')
      }

      },
    deleteChip(i) {
      this.selected.splice(i, 1)
    }
  },
  computed: {
    categories () {
      const search = this.search.toLowerCase()

      if (!search) return this.items

      return this.items.filter(item => {
        const text = item.text.toLowerCase()

        return text.indexOf(search) > -1
      })
    },
    selections () {
      const selections = []

      for (const selection of this.selected) {
        selections.push(selection)
      }

      return selections
    },
  }
}
</script>

